<template>
  <div class="app">
    <div v-if="state.account.nickname">
      <p>안녕하세요? {{ state.account.nickname }}님!</p>
      <button @click="logout()">로그아웃</button>
      <button @click="">스도쿠 게임하기</button>
      <MyTestGameView 
        v-if="sudokustart"
        @inputdata="sudokustart=false; inputdatas(timer)"
      />
    </div>
    <div v-else>
      <label for="nickname">
        <span>아이디</span>
        <input type="text" id="nickname" v-model="state.form.nickname" />
      </label>
      <button @click="submit()">로그인</button>
    </div>
  </div>
</template>

<script>
import { inject } from "vue";
import { reactive } from "vue";
import MyTestGameView from "./components/MyTestGameView.vue"

export default {
  name:"App",
  components:{
    MyTestGameView,
  },
  methods:{
    inputdatas(timer){
      let nickname = state.account.nickname
      let url = '/testa'
      axios({
        url:url,
        method:"PUT",
        data:{
          nickname:nickname,
          timer:timer
        }
      }).then( (res) => {
        console.log(res)
      })
    }
  },
  data(){
    return {
      sudokustart:false,
    }
  },
  setup() {
    const axios = inject('$axios');
    const state = reactive({
      account: {
        id: null,
        // name: "",
      },
      form: {
        nickname: "",
      },
    });

    const submit = () => {
      const args = {
        nickname: state.form.nickname,
      };
      axios
        .post("http://localhost:3001/api/account", args)
        .then((res) => {
          alert("로그인에 성공했습니다.");
          state.account.nickname = res.data;
          
        })
        .catch(() => {
          console.log(args);
          alert("로그인에 실패했습니다. 계정 정보를 확인해주세요.");
        });
    };

    const logout = () => {
      axios.delete("http://localhost:3001/api/account").then(() => {
        alert("로그아웃하였습니다.");
        state.account.nickname = null;
        state.form.nickname = '';
      });
    };

    return { state, submit, logout };
  },
};
</script>